import React, { useContext, useState } from "react";
import { makeStyles, TextField } from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import { ApplicationContext } from "../ApplicationContext.js";
import { Autocomplete } from "@material-ui/lab";
import * as S from "./ModalAddRefundIndividual.style.js";
import { MdAttachFile } from "react-icons/md";

const useStyles = makeStyles((theme) => ({
  root: {
    padding: theme.spacing(4),
    display: "flex",
    gap: "32px",
  },
}));

const ModalAddRefundGroupIndividual = ({ id, refundIdIndexGroup }) => {
  const {
    setShowRefundModalIndividual,
    showRefundModalIndividual,
    setallRefundsIndividual,
    allRefundsIndividual,
  } = useContext(ApplicationContext);

  const classes = useStyles();

  const handleCloseModal = () => {
    setShowRefundModalIndividual((old) => !old);
  };

  const defaultProps = {
    options: top100Films,
    getOptionLabel: (option) => option,
  };

  const [objToWork, setObjToWork] = useState(() => {
    if (refundIdIndexGroup) {
      const onlyRefundsFromId = allRefundsIndividual.filter(
        (refund) => refund.id === id
      );
      const onyThisRefundInfo = onlyRefundsFromId.filter(
        (refund) => refund.idIndex === refundIdIndexGroup
      );
      return onyThisRefundInfo[0];
    } else {
      return false;
    }
  });

  const [pax, setPax] = useState(() => {
    if (objToWork) {
      return objToWork.pax;
    } else {
      return null;
    }
  });

  const [price, setPrice] = useState(objToWork ? objToWork.price : "");
  const handleValueChange = (event, index) => {
    let valueComing = event.target.value;
    valueComing = valueComing.replace(/\D/g, "");
    valueComing = valueComing.replace(/(\d)(\d{2})$/, "$1,$2");
    valueComing = valueComing.replace(/(?=(\d{3})+(\D))\B/g, ".");
    setPrice(valueComing);
  };

  const [description, setDescription] = useState(
    objToWork ? objToWork.description : "Escreva aqui..."
  );
  const handleDescription = (event) => {
    setDescription(event.target.value && event.target.value);
  };

  const [fileName, setFileName] = useState(
    objToWork ? objToWork.fileName : undefined
  );
  const handleInputFile = (event) => {
    event.target.files.length
      ? setFileName(event.target.files[0].name)
      : setFileName("Nenhum arquivo selecionado");
  };

  //Save
  const handleAddRefundIndividual = () => {
    const obj = {
      id: id,
      idIndex: Date.now(),
      pax,
      price,
      fileName,
      description,
    };

    if (refundIdIndexGroup) {
      const newRefunds = allRefundsIndividual.map((refund) =>
        refund.idIndex === refundIdIndexGroup ? { ...obj } : { ...refund }
      );
      setallRefundsIndividual(newRefunds);
      window.localStorage.setItem(
        "allRefundsIndividual",
        JSON.stringify(newRefunds)
      );
    } else {
      const newRefunds = [...allRefundsIndividual, { ...obj }];
      setallRefundsIndividual(newRefunds);
      window.localStorage.setItem(
        "allRefundsIndividual",
        JSON.stringify(newRefunds)
      );
    }

    handleCloseModal();
  };

  return (
    <>
      <S.Mask showRefundModalIndividual={showRefundModalIndividual}></S.Mask>
      <S.Container
        showRefundModalIndividual={showRefundModalIndividual}
        className="container"
      >
        <header className="header-modal">
          <h1>Adicionar reembolso individual</h1>
        </header>

        <div
          className={classes.root + " content content-general"}
          style={{ height: "100%;" }}
        >
          <Grid container xs={12} alignContent="flex-start">
            <Grid item xs={12}>
              <h3>Opções</h3>
            </Grid>
            <Grid item xs={12}>
              <Autocomplete
                {...defaultProps}
                openOnFocus
                value={pax}
                onChange={(event, newValue) => {
                  setPax(newValue);
                }}
                renderInput={(params) => (
                  <TextField
                    {...params}
                    label="Nome do passageiro"
                    margin="normal"
                  />
                )}
              />
            </Grid>
            <Grid item xs={12} style={{ marginTop: "16px" }}>
              <label className="label-input-number">
                <span>Valor reembolso</span>
                <input
                  type="text"
                  className="input-number"
                  name="reembolso"
                  value={price}
                  onChange={(event) => handleValueChange(event)}
                />
              </label>
            </Grid>
            <Grid item xs={12} style={{ marginTop: "32px" }}>
              <TextField
                id="standard-multiline-static"
                label="Detalhes"
                className={"w-100"}
                multiline
                rows={4}
                defaultValue="Escreva aqui..."
                value={description}
                onChange={handleDescription}
              />
            </Grid>
            <Grid item xs={12} style={{ marginTop: "32px" }}>
              <h3>Comprovante/Foto</h3>
              <label className="label-input-file">
                <input
                  type="file"
                  onChange={(event) => handleInputFile(event)}
                />
                <MdAttachFile size={24} color={"#000"} />
                <span>{fileName ? fileName : "Anexar arquivo"}</span>
                <span className="fake-button btn btn-secondary">
                  Selecionar
                </span>
              </label>
            </Grid>
          </Grid>

          <footer className="footer">
            <button className="btn btn-close" onClick={handleCloseModal}>
              Fechar
            </button>
            <button
              className="btn btn-primary"
              onClick={handleAddRefundIndividual}
            >
              Adicionar
            </button>
          </footer>
        </div>
      </S.Container>
    </>
  );
};

