import React, { useState, createContext, useEffect } from "react";

export const ApplicationContext = createContext();

export const ApplicationStorage = ({ children }) => {
  //modals
  const [showMenuInstructions, setShowMenuInstructions] = useState(false);
  const [hideInstructions, setHideInstructions] = useState(true);
  const [showModalIncident, setShowModalIncident] = useState(false);
  const [showRefundModal, setShowRefundModal] = useState(false);
  const [showRefundModalIndividual, setShowRefundModalIndividual] =
    useState(false);

  const [allIncidents, setAllIncidents] = useState(() => {
    const _allIncidents = window.localStorage.getItem("allIncidents");

    if (_allIncidents) {
      return JSON.parse(_allIncidents);
    }
    return [];
  });

  const [allRefundsGroup, setallRefundsGroup] = useState(() => {
    const _allRefundsGroup = window.localStorage.getItem("allRefundsGroup");

    if (_allRefundsGroup) {
      return JSON.parse(_allRefundsGroup);
    }
    return [];
  });

  const [allRefundsIndividual, setallRefundsIndividual] = useState(() => {
    const _allRefundsIndividual = window.localStorage.getItem(
      "allRefundsIndividual"
    );

    if (_allRefundsIndividual) {
      return JSON.parse(_allRefundsIndividual);
    }
    return [];
  });

  useEffect(() => {
    console.log("allIncidents", allIncidents);
    console.log("allRefundsGroup", allRefundsGroup);
    console.log("allRefundsIndividual", allRefundsIndividual);
  }, [allIncidents, allRefundsGroup, allRefundsIndividual]);

  return (
    <ApplicationContext.Provider
      value={{
        showRefundModalIndividual,
        setShowRefundModalIndividual,
        showMenuInstructions,
        hideInstructions,
        showModalIncident,
        showRefundModal,
        setHideInstructions,
        setShowMenuInstructions,
        setShowModalIncident,
        setShowRefundModal,
        allIncidents,
        setAllIncidents,
        allRefundsGroup,
        setallRefundsGroup,
        allRefundsIndividual,
        setallRefundsIndividual,
      }}
    >
      {children}
    </ApplicationContext.Provider>
  );
};
