import React, { useContext, useEffect } from "react";
import { ApplicationContext } from "../ApplicationContext";
import * as S from "./Result.style.js";

const Home = () => {
  const { setHideInstructions } = useContext(ApplicationContext);

  useEffect(() => {
    setHideInstructions(true);
  }, [setHideInstructions]);

  return (
    <S.Wrapper>
      <div></div>
    </S.Wrapper>
  );
};

export default Home;
