import {
  FormControl,
  MenuItem,
  TextField,
  InputLabel,
  Select,
  FormControlLabel,
  Checkbox,
  makeStyles,
} from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import React, { useContext, useState } from "react";
import { formatValue } from "../utils";
import ModalAddRefundGroup from "../components/ModalAddRefundGroup";
import { ApplicationContext } from "../ApplicationContext.js";
import ModalAddRefundGroupIndividual from "./ModalAddRefundIndividual";
import { MdCreate, MdDelete } from "react-icons/md";
import DateFnsUtils from "@date-io/date-fns";
import {
  KeyboardDatePicker,
  MuiPickersUtilsProvider,
} from "@material-ui/pickers";
import * as S from "./ModalAddIncident.style.js";

const useStyles = makeStyles((theme) => ({
  root: {
    padding: theme.spacing(4),
    display: "flex",
    flexDirection: "column",
    gap: "16px",
  },
}));

const ModalAddIncident = ({ id, customIndex }) => {
  const classes = useStyles();

  const {
    showModalIncident,
    setShowModalIncident,
    setShowRefundModal,
    showRefundModal,
    setAllIncidents,
    allIncidents,
    allRefundsGroup,
    setShowRefundModalIndividual,
    showRefundModalIndividual,
    allRefundsIndividual,
    setallRefundsIndividual,
    setallRefundsGroup,
  } = useContext(ApplicationContext);

  const [dataInicioViagem, setDataInicioViagem] = useState(
    typeof customIndex === "number"
      ? allIncidents[customIndex].generalInfo.dataInicioViagem
      : new Date("2014-08-18T21:11:54")
  );
  const handleDataInicioViagem = (date) => {
    setDataInicioViagem(date);
  };

  const [horarioInicioViagem, setHorarioInicioViagem] = useState(
    typeof customIndex === "number"
      ? allIncidents[customIndex].generalInfo.horarioInicioViagem
      : ""
  );
  const handleHorarioInicioViagem = (event) => {
    setHorarioInicioViagem(event.target.value);
  };

  const [dataInformadoBo, setDataInformadoBo] = useState(
    typeof customIndex === "number"
      ? allIncidents[customIndex].generalInfo.dataInformadoBo
      : ""
  );
  const handleDataInformadoBo = (event) => {
    setDataInformadoBo(event.target.value);
  };

  const [horarioInformadoBo, setHorarioInformadoBo] = useState(
    typeof customIndex === "number"
      ? allIncidents[customIndex].generalInfo.horarioInformadoBo
      : ""
  );
  const handleHorarioInformadoBo = (event) => {
    setHorarioInformadoBo(event.target.value);
  };

  const [analista, setAnalista] = useState(
    typeof customIndex === "number"
      ? allIncidents[customIndex].generalInfo.analista
      : ""
  );
  const handleChangeAnalista = (event) => {
    setAnalista(event.target.value);
  };

  const [incident, setIncident] = useState(
    typeof customIndex === "number"
      ? allIncidents[customIndex].generalInfo.incident
      : ""
  );
  const handleChangeIncidentType = (event) => {
    setIncident(event.target.value);
  };

  const [travarGrupo, setTravarGrupo] = useState(
    typeof customIndex === "number"
      ? allIncidents[customIndex].generalInfo.travarGrupo
      : false
  );
  const handleChangeTravarGrupo = (event) => {
    setTravarGrupo(event.target.checked);
  };

  const [empresaAvisou, setEmpresaAvisou] = useState(
    typeof customIndex === "number"
      ? allIncidents[customIndex].generalInfo.empresaAvisou
      : false
  );
  const handleEmpresaAvisou = (event) => {
    setEmpresaAvisou(event.target.checked);
  };

  const [riscoAtuar, setRiscoAtuar] = useState(
    typeof customIndex === "number"
      ? allIncidents[customIndex].generalInfo.riscoAtuar
      : ""
  );
  const handleRiscoAtuar = (event) => {
    setRiscoAtuar(event.target.checked);
  };

  //inputs Buser experience
  const [analistaBxGrupo, setAnalistaBxGrupo] = useState(
    typeof customIndex === "number"
      ? allIncidents[customIndex].bxInfo.analistaBxGrupo
      : ""
  );
  const handleChangeAnalistaBx = (event) => {
    setAnalistaBxGrupo(event.target.value);
  };

  const [dataBo, setDataBo] = useState(
    typeof customIndex === "number"
      ? allIncidents[customIndex].bxInfo.dataBo
      : ""
  );
  const handleChangeDataBo = (event) => {
    setDataBo(event.target.value);
  };

  //inputs trativa individual
  const [analistaBxIndividual, setAnalistaBxIndividual] = useState(
    typeof customIndex === "number"
      ? allIncidents[customIndex].bxInfo.analistaBxIndividual
      : ""
  );
  const handleChangeAnalistaIndividual = (event) => {
    setAnalistaBxIndividual(event.target.value);
  };

  const [dataBoIndividual, setDataBoIndividual] = useState(
    typeof customIndex === "number"
      ? allIncidents[customIndex].bxInfo.dataBoIndividual
      : ""
  );
  const handleChangeDataBoIndividual = (event) => {
    setDataBoIndividual(event.target.value);
  };

  //Open refund group
  const handleAddRefundGroup = () => {
    setRefundIdIndexComing(undefined);
    setShowRefundModal((old) => !old);
  };

  const handleAddRefundIndividual = () => {
    setRefundIdIndexComing(undefined);
    setShowRefundModalIndividual((old) => !old);
  };

  //close modal incident
  const handleCloseModal = () => {
    setShowModalIncident((old) => !old);
  };

  //Save
  const handleSaveIncident = () => {
    const obj = {
      id: id,
      generalInfo: {
        dataInicioViagem,
        horarioInicioViagem,
        dataInformadoBo,
        horarioInformadoBo,
        analista,
        incident,
        travarGrupo,
        empresaAvisou,
        riscoAtuar,
      },
      bxInfo: {
        analistaBxGrupo,
        dataBo,
        analistaBxIndividual,
        dataBoIndividual,
      },
    };

    if (typeof customIndex === "number") {
      const newIncidents = allIncidents.map((incident, index) =>
        index === customIndex ? { ...obj } : { ...incident }
      );
      setAllIncidents(newIncidents);
      window.localStorage.setItem("allIncidents", JSON.stringify(newIncidents));
    } else {
      const newIncidents = [...allIncidents, { ...obj }];
      setAllIncidents(newIncidents);
      window.localStorage.setItem("allIncidents", JSON.stringify(newIncidents));
    }

    handleCloseModal();
  };

  const verififyRefunds = () => {
    let refund = false;
    allRefundsGroup.forEach((element) => {
      if (element.id === id) {
        refund = true;
      }
    });

    return refund;
  };

  const verififyRefundsIndividual = () => {
    let refundIndividual = false;
    allRefundsIndividual.forEach((element) => {
      if (element.id === id) {
        refundIndividual = true;
      }
    });

    return refundIndividual;
  };

  const handleDeleteRefundsGroup = (refundIdIndex) => {
    const newAllRefundsGroup = allRefundsGroup.filter(
      (item, index) => item.idIndex !== refundIdIndex
    );
    window.localStorage.setItem(
      "allRefundsGroup",
      JSON.stringify(newAllRefundsGroup)
    );
    setallRefundsGroup(newAllRefundsGroup);
  };

  const handleDeleteRefundIndividual = (refundIdIndex) => {
    const newAllRefundsIndividual = allRefundsIndividual.filter(
      (item, index) => item.idIndex !== refundIdIndex
    );
    setallRefundsIndividual(newAllRefundsIndividual);
    window.localStorage.setItem(
      "allRefundsIndividual",
      JSON.stringify(newAllRefundsIndividual)
    );
  };

  const [refundIdIndexGroup, setRefundIdIndexComing] = useState(undefined);
  const handleEditRefundGroup = (refundIdIndex) => {
    setRefundIdIndexComing(refundIdIndex);
    setShowRefundModal((old) => !old);
  };

  const handleEditRefundIndividual = (refundIdIndex) => {
    setRefundIdIndexComing(refundIdIndex);
    setShowRefundModalIndividual((old) => !old);
  };

  return (
    <>
      {showRefundModal && (
        <ModalAddRefundGroup id={id} refundIdIndexGroup={refundIdIndexGroup} />
      )}
      {showRefundModalIndividual && (
        <ModalAddRefundGroupIndividual
          id={id}
          refundIdIndexGroup={refundIdIndexGroup}
        />
      )}

      <S.Mask showModalIncident={showModalIncident}></S.Mask>
      <S.Container
        showModalIncident={showModalIncident}
        setShowModalIncident={setShowModalIncident}
        className="container"
      >
        <header className="header-modal">
          <h1>
            {typeof customIndex === "number"
              ? "Editar incidente"
              : "Registrar Incidente"}
          </h1>
        </header>
        <input
          type="radio"
          name="menu"
          className="input-general"
          id="general"
          defaultChecked
        />
        <input type="radio" name="menu" className="input-bx" id="bx" />
        <input type="radio" name="menu" className="input-risk" id="risk" />
        <header className="header-content">
          <label className="label-general" htmlFor="general">
            Informações Gerais
          </label>
          <label className="label-bx" htmlFor="bx">
            Buser Experience
          </label>
          <label className="label-risk" htmlFor="risk">
            Risco
          </label>
        </header>
        <div className={classes.root + " content content-general"}>
          <Grid container spacing={3}>
            <Grid item xs={12}>
              <h3>Informações Gerais</h3>
            </Grid>
            <Grid item xs={4}>
              <MuiPickersUtilsProvider utils={DateFnsUtils}>
                <KeyboardDatePicker
                  disableToolbar
                  variant="inline"
                  format="dd/MM/yyyy"
                  margin="normal"
                  id="date-picker-inline"
                  label="Data início da viagem"
                  value={dataInicioViagem}
                  onChange={handleDataInicioViagem}
                  KeyboardButtonProps={{
                    "aria-label": "change date",
                  }}
                />
              </MuiPickersUtilsProvider>
            </Grid>
            <Grid item xs={4}>
              <TextField
                className={"w-100"}
                label="Horário início da viagem"
                type="time"
                value={horarioInicioViagem}
                onChange={handleHorarioInicioViagem}
                InputLabelProps={{
                  shrink: true,
                }}
                inputProps={{
                  step: 300, // 5 min
                }}
              />
            </Grid>
          </Grid>
          <Grid container spacing={3}>
            <Grid item xs={4}>
              <TextField
                className={"w-100"}
                label="Data que foi informado o BO"
                type="date"
                value={dataInformadoBo}
                onChange={handleDataInformadoBo}
                InputLabelProps={{
                  shrink: true,
                }}
              />
            </Grid>
            <Grid item xs={4}>
              <TextField
                className={"w-100"}
                label="Horário que foi informado o BO"
                type="time"
                value={horarioInformadoBo}
                onChange={handleHorarioInformadoBo}
                InputLabelProps={{
                  shrink: true,
                }}
                inputProps={{
                  step: 300, // 5 min
                }}
              />
            </Grid>
          </Grid>
          <Grid container spacing={3}>
            <Grid item xs={4}>
              <FormControl className={"w-100"}>
                <InputLabel id="demo-simple-select-label">
                  Analista responsável
                </InputLabel>
                <Select
                  labelId="demo-simple-select-label"
                  id="demo-simple-select"
                  value={analista}
                  onChange={handleChangeAnalista}
                >
                  <MenuItem value={"Cleber Bambam"}>Cleber Bambam</MenuItem>
                  <MenuItem value={"Jorge Mateus"}>Jorge Mateus</MenuItem>
                  <MenuItem value={"Rodrigo Rodrigão"}>
                    Rodrigo Rodrigão
                  </MenuItem>
                  <MenuItem value={"Buser Analista teste"}>
                    Buser Analista teste
                  </MenuItem>
                  <MenuItem value={"Tyrone Junqueira"}>
                    Tyrone Junqueira
                  </MenuItem>
                  <MenuItem value={"Lucas Roque"}>Lucas Roque</MenuItem>
                </Select>
              </FormControl>
            </Grid>
          </Grid>
          <Grid container spacing={3} direction="column">
            <Grid item xs={4}>
              <FormControl className="w-100">
                <InputLabel id="demo-simple-select-label">
                  Categoria do incidente
                </InputLabel>
                <Select
                  labelId="demo-simple-select-label"
                  value={incident}
                  onChange={handleChangeIncidentType}
                >
                  <MenuItem value={"Acidente"}>Acidente</MenuItem>
                  <MenuItem value={"Apreensão"}>Apreensão</MenuItem>
                  <MenuItem value={"Atraso"}>Atraso</MenuItem>
                  <MenuItem value={"Deslocamento"}>Deslocamento</MenuItem>
                  <MenuItem value={"Encomendas"}>Encomendas</MenuItem>
                  <MenuItem value={"Falha mecânica"}>Falha mecânica</MenuItem>
                  <MenuItem value={"Multas avulsas"}>Multas avulsas</MenuItem>
                  <MenuItem value={"Notificação"}>Notificação</MenuItem>
                  <MenuItem value={"No show"}>No show</MenuItem>
                  <MenuItem value={"Passageiro"}>Passageiro</MenuItem>
                  <MenuItem value={"Pneu"}>Pneu</MenuItem>
                  <MenuItem value={"Rodoviária"}>Rodoviária</MenuItem>
                  <MenuItem value={"Outros"}>Outros</MenuItem>
                </Select>
              </FormControl>
            </Grid>
          </Grid>
          <Grid container spacing={0} direction="column">
            <Grid item xs={4}>
              <FormControlLabel
                value="travar-grupo"
                control={
                  <Checkbox
                    color="primary"
                    checked={travarGrupo}
                    onChange={handleChangeTravarGrupo}
                  />
                }
                label="Travar grupo"
              />
            </Grid>
            <Grid item xs={4}>
              <FormControlLabel
                value="empresa-avisou"
                control={
                  <Checkbox
                    color="primary"
                    checked={empresaAvisou}
                    onChange={handleEmpresaAvisou}
                  />
                }
                label="A empresa avisou"
              />
            </Grid>
            <Grid item xs={4}>
              <FormControlLabel
                value="risco-atuar"
                control={
                  <Checkbox
                    color="primary"
                    checked={riscoAtuar}
                    onChange={handleRiscoAtuar}
                  />
                }
                label="Risco precisa atuar neste caso"
              />
            </Grid>
          </Grid>
          <footer className="footer">
            <button className="btn btn-close" onClick={handleCloseModal}>
              Fechar
            </button>
            <button className="btn btn-primary" onClick={handleSaveIncident}>
              Salvar
            </button>
          </footer>
        </div>
        <div className={classes.root + " content content-bx"}>
          <Grid container spacing={3}>
            <Grid item xs={12}>
              <h3>Tratativas de grupo</h3>
            </Grid>
          </Grid>

          <Grid container spacing={3} style={{ marginTop: "4px" }}>
            <Grid item xs={12}>
              <Grid
                item
                xs={12}
                style={{
                  display: "flex",
                  flexDirection: "row",
                  alignItems: "flex-end",
                  justifyContent: "flex-start",
                  gap: "16px",
                }}
              >
                <Grid item xs={2}>
                  <FormControl className="w-100">
                    <InputLabel id="demo-simple-select-label">
                      Nome do analista
                    </InputLabel>
                    <Select
                      labelId="demo-simple-select-label"
                      id="demo-simple-select"
                      value={analistaBxGrupo}
                      onChange={handleChangeAnalistaBx}
                    >
                      <MenuItem value={"Cleber Bambam"}>Cleber Bambam</MenuItem>
                      <MenuItem value={"Jorge Mattos"}>Jorge Mattos</MenuItem>
                      <MenuItem value={"Rodrigo Rodrigão"}>
                        Rodrigo Rodrigão
                      </MenuItem>
                      <MenuItem value={"Buser Analista teste"}>
                        Buser Analista teste
                      </MenuItem>
                      <MenuItem value={"Tyrone Junqueira"}>
                        Tyrone Junqueira
                      </MenuItem>
                      <MenuItem value={"Lucas Roque"}>Lucas Roque</MenuItem>
                    </Select>
                  </FormControl>
                </Grid>

                <button
                  className="btn btn-secondary"
                  style={{ marginLeft: "auto" }}
                  onClick={handleAddRefundGroup}
                >
                  Adicionar reembolso ao grupo
                </button>
              </Grid>
            </Grid>
            <Grid
              item
              xs={12}
              style={{
                display: "flex",
                flexDirection: "column",
                alignItems: "flex-end",
              }}
            >
              {allRefundsGroup.length && verififyRefunds() ? (
                <div className="table table-refund">
                  <div className="content-table">
                    <header className="line">
                      <p className="item">id</p>
                      <p className="item">multiTrecho</p>
                      <p className="item">tempoParado</p>
                      <p className="item">Description</p>
                      <p className="item">Total</p>
                      <p className="item"></p>
                    </header>
                    {allRefundsGroup
                      .filter((item, index) => item.id === id)
                      .map((refund) => (
                        <div className="line" key={refund.idIndex}>
                          <p className="item">{refund.id}</p>
                          <p className="item">{refund.multiTrechoName}</p>
                          <p className="item">{refund.tempoParado}</p>
                          <p className="item">{refund.description}</p>
                          <p className="item">{formatValue(refund.total)}</p>

                          <div className="item flex-area j-content-end">
                            <button
                              className="action-bt-line"
                              onClick={() =>
                                handleEditRefundGroup(refund.idIndex)
                              }
                            >
                              <MdCreate size={22} />
                            </button>
                            <button
                              className="action-bt-line"
                              onClick={() =>
                                handleDeleteRefundsGroup(refund.idIndex)
                              }
                            >
                              <MdDelete size={22} />
                            </button>
                          </div>
                        </div>
                      ))}
                  </div>
                </div>
              ) : (
                <div className="container-refound-group">
                  <p className="empty-message">
                    Nenhum reembolso adicionado ao grupo
                  </p>
                </div>
              )}
            </Grid>
          </Grid>

          <Grid container spacing={3} style={{ marginTop: "32px" }}>
            <Grid item xs={12}>
              <h3>Tratativa individual</h3>
            </Grid>
          </Grid>

          <Grid container spacing={3} style={{ marginTop: "4px" }}>
            <Grid
              item
              xs={12}
              style={{
                display: "flex",
                flexDirection: "row",
                alignItems: "flex-end",
                justifyContent: "flex-start",
                gap: "16px",
              }}
            >
              <Grid item xs={2}>
                <FormControl className="w-100">
                  <InputLabel id="demo-simple-select-label">
                    Nome do analista
                  </InputLabel>
                  <Select
                    labelId="demo-simple-select-label"
                    id="demo-simple-select"
                    value={analistaBxIndividual}
                    onChange={handleChangeAnalistaIndividual}
                  >
                    <MenuItem value={"Cleber Bambam"}>Cleber Bambam</MenuItem>
                    <MenuItem value={"Jorge Mattos"}>Jorge Mattos</MenuItem>
                    <MenuItem value={"Rodrigo Rodrigão"}>
                      Rodrigo Rodrigão
                    </MenuItem>
                    <MenuItem value={"Buser Analista teste"}>
                      Buser Analista teste
                    </MenuItem>
                    <MenuItem value={"Tyrone Junqueira"}>
                      Tyrone Junqueira
                    </MenuItem>
                    <MenuItem value={"Lucas Roque"}>Lucas Roque</MenuItem>
                  </Select>
                </FormControl>
              </Grid>
              <button
                className="btn btn-secondary"
                style={{ marginLeft: "auto" }}
                onClick={handleAddRefundIndividual}
              >
                Adicionar reembolso individual
              </button>
            </Grid>
            <Grid
              item
              xs={12}
              style={{
                display: "flex",
                flexDirection: "column",
                alignItems: "flex-end",
              }}
            >
              {allRefundsIndividual.length && verififyRefundsIndividual() ? (
                <div className="table table-refund">
                  <div className="content-table">
                    <header className="line">
                      <p className="item">id</p>
                      <p className="item">Nome Pax</p>
                      <p className="item">Valor</p>
                      <p className="item">Description</p>
                      <p className="item"></p>
                    </header>
                    {allRefundsIndividual
                      .filter((item, index) => item.id === id)
                      .map((refund) => (
                        <div className="line" key={refund.idIndex}>
                          <p className="item">{refund.id}</p>
                          <p className="item">{refund.pax}</p>
                          <p className="item">{refund.price}</p>
                          <p className="item">{refund.description}</p>

                          <div className="item flex-area j-content-end">
                            <button
                              className="action-bt-line"
                              onClick={() =>
                                handleEditRefundIndividual(refund.idIndex)
                              }
                            >
                              <MdCreate size={22} />
                            </button>
                            <button
                              className="action-bt-line"
                              onClick={() =>
                                handleDeleteRefundIndividual(refund.idIndex)
                              }
                            >
                              <MdDelete size={22} />
                            </button>
                          </div>
                        </div>
                      ))}
                  </div>
                </div>
              ) : (
                <div className="container-refound-group">
                  <p className="empty-message">
                    Nenhum reembolso adicionado ao grupo
                  </p>
                </div>
              )}
            </Grid>
          </Grid>

          <footer className="footer">
            <button className="btn btn-close" onClick={handleCloseModal}>
              Fechar
            </button>
            <button className="btn btn-primary" onClick={handleSaveIncident}>
              Salvar
            </button>
          </footer>
        </div>
        <div className={classes.root + " content content-risk"}>
          <Grid container spacing={3}>
            <Grid item xs={12}>
              <h3>Risco</h3>
            </Grid>
            <Grid item xs={12}>
              <p>Está parte não será testada agora</p>
            </Grid>
          </Grid>
          <footer className="footer">
            <button className="btn btn-close" onClick={handleCloseModal}>
              Fechar
            </button>
            <button className="btn btn-primary">Salvar</button>
          </footer>
        </div>
      </S.Container>
    </>
  );
};

export default ModalAddIncident;
