import React from "react";
import { Checkbox } from "@material-ui/core";
import { formatValue } from "../utils";
import { MdAttachFile, MdLock } from "react-icons/md";
import * as S from "./ListPax.style.js";

const ListPax = ({ multiTrecho, setMultiTrecho, total, setTotal }) => {
  const handleValueChange = (event, index) => {
    let valueComing = event.target.value;
    valueComing = valueComing.replace(/\D/g, "");
    valueComing = valueComing.replace(/(\d)(\d{2})$/, "$1,$2");
    valueComing = valueComing.replace(/(?=(\d{3})+(\D))\B/g, ".");

    const newMultiTrecho = multiTrecho.map((item, indexItem) =>
      indexItem === index
        ? {
            ...item,
            [event.target.name]: event.target.value ? valueComing : 0,
          }
        : { ...item }
    );
    setMultiTrecho(newMultiTrecho);
    // calcTotal(newMultiTrecho);
  };

  // const calcTotal = (newMultiTrecho) => {
  //   const allValues = newMultiTrecho.map((item) => {
  //     return [
  //       item.reembolsoTempo,
  //       item.voucher,
  //       item.lanche,
  //       item.reembolsoManual,
  //     ];
  //   });

  //   const flatValues = allValues.flat(Infinity);

  //   const total = flatValues.reduce((a, b) => {
  //     const aString = `${a}`;
  //     const bString = `${b}`;

  //     return (
  //       parseFloat(aString.replace(",", ".")) +
  //       parseFloat(bString.replace(",", "."))
  //     );
  //   });

  //   setTotal(total);
  // };

  const handleCheckAll = (event) => {
    setMultiTrecho((old) =>
      old.map((item, index) => ({ ...item, checked: event.target.checked }))
    );
  };

  const handleChangeChecked = (event, indexComing) => {
    setMultiTrecho((old) =>
      old.map((item, index) =>
        index === indexComing ? { ...item, checked: !item.checked } : item
      )
    );
  };

  return (
    <S.Container multiTrecho={multiTrecho}>
      {!multiTrecho ? (
        <p className="empty-message">
          Selecione um multi-trecho para ver os passageiros
        </p>
      ) : (
        <div className="table-container">
          <header>
            <div className="item pax-checkbox">
              <Checkbox
                inputProps={{ "aria-label": "uncontrolled-checkbox" }}
                onChange={(event) => handleCheckAll(event)}
              />
            </div>
            <div className="item pax-name">
              <p>Passageiro</p>
            </div>
            <div className="item pax-pass">
              <p>Passagem</p>
            </div>
            <div className="item pax-class">
              <p>Classe</p>
            </div>
            <div className="item pax-refund-tempo">
              <p>
                Reembolso por tempo parado <MdLock />
              </p>
            </div>
            <div className="item pax-refund-manual">
              <p>Reembolso manual</p>
            </div>
            <div className="item pax-voucher">
              <p>Voucher</p>
            </div>
            <div className="item pax-lanche">
              <p>Lanche</p>
            </div>
          </header>
          <div className="body-table">
            {multiTrecho.map((pax, index) => (
              <div className="line" key={index}>
                <div className="item pax-checkbox">
                  <Checkbox
                    checked={pax.checked}
                    onChange={(event) => handleChangeChecked(event, index)}
                    inputProps={{ "aria-label": "primary checkbox" }}
                  />
                </div>
                <div className="item pax-name">
                  <p>{pax.name}</p>
                </div>
                <div className="item pax-pass">
                  <p>{formatValue(pax.passagem)}</p>
                </div>
                <div className="item pax-class">
                  <p>{pax.classe}</p>
                </div>
                <div className="item pax-refund-tempo">
                  <label>
                    <input
                      type="text"
                      className="input-number-refund"
                      value={pax.reembolsoTempo}
                      disabled
                      name="reembolsoTempo"
                    />
                  </label>
                </div>
                <div className="item pax-refund-manual">
                  <label>
                    <input
                      type="text"
                      className="input-number-refund"
                      value={pax.reembolsoManual}
                      name="reembolsoManual"
                      onChange={(event) => handleValueChange(event, index)}
                    />
                  </label>
                </div>
                <div className="item pax-voucher">
                  <label>
                    <input
                      type="text"
                      className="input-number"
                      value={pax.voucher}
                      name="voucher"
                      onChange={(event) => handleValueChange(event, index)}
                    />
                  </label>
                </div>
                <div className="item pax-lanche">
                  <button type="button">
                    <MdAttachFile size={20} color={"#000"} />
                  </button>
                  <label>
                    <input
                      type="text"
                      className="input-number"
                      value={pax.lanche}
                      name="lanche"
                      onChange={(event) => handleValueChange(event, index)}
                    />
                  </label>
                </div>
              </div>
            ))}
          </div>
          <div className="container-total">
            <p className="title">Total:</p>
            <p className="total"> {formatValue(total)}</p>
          </div>
        </div>
      )}
    </S.Container>
  );
};

export default ListPax;
