import React, { useContext, useEffect, useState } from "react";
import { ApplicationContext } from "../ApplicationContext";
import ModalAddIncident from "../components/ModalAddIncident";
import IncidentesGrupo1 from "../img/incidentes-grupo 1.png";
import IncidentesGrupo2 from "../img/incidentes-grupo 2.png";
import IncidentesGrupo3 from "../img/incidentes-grupo 3.png";
import { MdCreate, MdDelete } from "react-icons/md";

import * as S from "./Test.style.js";

const Test = () => {
  const {
    setHideInstructions,
    setShowModalIncident,
    showModalIncident,
    allIncidents,
    setAllIncidents,
    allRefundsGroup,
  } = useContext(ApplicationContext);

  useEffect(() => {
    setHideInstructions(false);
    if (!allIncidents.length) {
      window.localStorage.clear();
    }
  }, [setHideInstructions, allIncidents]);

  const [customIndex, setCustomIndex] = useState(undefined);
  const [id, setId] = useState(undefined);

  const handleOpenModalRegisterIncidente = () => {
    setId(Date.now());
    setCustomIndex(null);
    setShowModalIncident((old) => !old);
  };

  const handleEdit = (incidentId, indexComing) => {
    setId(incidentId);
    setCustomIndex(indexComing);
    setShowModalIncident((old) => !old);
  };

  const handleDelete = (indexComing) => {
    const newAllIncidents = allIncidents.filter(
      (item, index) => indexComing !== index
    );
    const newallRefundsGroup = allRefundsGroup.filter(
      (item, index) => indexComing !== index
    );
    window.localStorage.setItem(
      "allIncidents",
      JSON.stringify(newAllIncidents)
    );
    window.localStorage.setItem(
      "allRefundsGroup",
      JSON.stringify(newallRefundsGroup)
    );
    setAllIncidents(newAllIncidents);
  };

  return (
    <>
      {showModalIncident && (
        <ModalAddIncident id={id} customIndex={customIndex} />
      )}
      <S.Wrapper>
        <div className="container">
          <img src={IncidentesGrupo1} alt="Staff Grupos" />
          <div className="container-search-button">
            <img src={IncidentesGrupo2} alt="Staff Grupos" />
            <button onClick={handleOpenModalRegisterIncidente}>
              Registrar incidente
            </button>
          </div>
          {allIncidents.length ? (
            <div className="table">
              <div className="content-table">
                <header className="line">
                  <p className="item">id</p>
                  <p className="item">Data Informado</p>
                  <p className="item">Horário Informado</p>
                  <p className="item">Analista</p>
                  <p className="item">Categoria</p>
                  <p className="item">Travou grupo</p>
                  <p className="item">Empresa avisou</p>
                  <p className="item">Risco atuar</p>
                  <p className="item"></p>
                </header>

                {allIncidents.map((incident, index) => (
                  <div className="line" key={incident.id}>
                    <p className="item">{incident.id}</p>
                    <p className="item">
                      {incident.generalInfo.dataInformadoBo}
                    </p>
                    <p className="item">
                      {incident.generalInfo.horarioInformadoBo}
                    </p>
                    <p className="item">{incident.generalInfo.analista}</p>
                    <p className="item">{incident.generalInfo.incident}</p>
                    <p className="item">
                      {incident.generalInfo.travarGrupo === true
                        ? "sim"
                        : "não"}
                    </p>
                    <p className="item">
                      {incident.generalInfo.empresaAvisou === true
                        ? "sim"
                        : "não"}
                    </p>
                    <p className="item">
                      {incident.generalInfo.riscoAtuar === true ? "sim" : "não"}
                    </p>
                    <div className="item flex-area j-content-end">
                      <button
                        className="action-bt-line"
                        onClick={() => handleEdit(incident.id, index)}
                      >
                        <MdCreate size={22} />
                      </button>
                      <button
                        className="action-bt-line"
                        onClick={() => handleDelete(index)}
                      >
                        <MdDelete size={22} />
                      </button>
                    </div>
                  </div>
                ))}
              </div>
            </div>
          ) : (
            <img src={IncidentesGrupo3} alt="Staff Grupos" />
          )}
        </div>
      </S.Wrapper>
    </>
  );
};

export default Test;
